import todocon from './class.js'

let count = 0;

export default class todoc {
    constructor(){
        this.collection = []
    }
    add(title,complete) {
        const struction = todocon(title,complete);
        this.collection.push(struction)
    }
    addto(title,complete){
        const itemid = count;
       const Listtodo = document.getElementById('listtodo');
       const main = document.createElement('li');
       main.classList.add('projectli');
       main.id = itemid;
       const checkbox = document.createElement('input');
       checkbox.type = 'checkbox';
       checkbox.completed = complete;
       const maintext = document.createElement('h3');
       maintext.innerText = title;
       main.append(
         checkbox,
         maintext,
       );
       Listtodo.append(main);
       count +=1
    }
}